.benz{
    height: 100%;
    width: 100%;
}
@keyframes tonext {
    75% {
      left: 0;
    }
    95% {
      left: 100%;
    }
    98% {
      left: 100%;
    }
    99% {
      left: 0;
    }
  }
  
  @keyframes tostart {
    75% {
      left: 0;
    }
    95% {
      left: -300%;
    }
    98% {
      left: -300%;
    }
    99% {
      left: 0;
    }
  }
  
  @keyframes snap {
    96% {
      scroll-snap-align: center;
    }
    97% {
      scroll-snap-align: none;
    }
    99% {
      scroll-snap-align: none;
    }
    100% {
      scroll-snap-align: center;
    }
  }
  * {
    box-sizing: border-box;
    scrollbar-color: transparent transparent; /* thumb and track color */
    scrollbar-width: 0px;
      scrollbar-color: dark;
    
  }
  
   ::-webkit-scrollbar {
    width: 0;
  }
    *::-webkit-scrollbar-track {
    background: transparent;
  } 
  
  *::-webkit-scrollbar-thumb {
    background: transparent;
    border: none;
  }  
  
   * {
    -ms-overflow-style: none;
  } 
   
  ol, li {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .slideshow {
    position: relative;
    padding-top: 42.5%;
    filter: drop-shadow(0 0 10px #0003);
    perspective: 100px;
    overflow: hidden;
  }
  
  .show {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    overflow-x: scroll;
    counter-reset: item;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
  }
  
  .slides {
    position: relative;
    flex: 0 0 100%;
    width: 100%;
    counter-increment: item;
  }  
  .slides:before {
    content: counter(item);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-40%,70px);
    color: #fff;
    font-size: 2em;
  }
  
  .container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    scroll-snap-align: center;
  }
  
  @media (hover: hover) {
    .container {
      animation-name: tonext, snap;
      animation-timing-function: ease;
      animation-duration: 4s;
      animation-iteration-count: infinite;
    }
  
    .slides:last-child .container {
      animation-name: tostart, snap;
    }
  }
  
  @media (prefers-reduced-motion: reduce) {
    .container{
      animation-name: none;
    }
  }
  
  .slideshow:hover .container,
  .slideshow:focus-within .container {
    animation-name: none;
  }
  
  .slideshow__navigation {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: center;
  }
  
  .slideshow__navigation-list,
  .slideshow__navigation-item {
    display: inline-block;
  }
  
  .slideshow__navigation-button {
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #333;
    background-clip: content-box;
    border: 0.25rem solid transparent;
    border-radius: 50%;
    font-size: 0;
    transition: transform 0.1s;
  }
  
  .slideshow::before,
  .slideshow::after,
  .containerback,
  .containernext {
    position: absolute;
    top: 0;
    margin-top: 18.75%;
    width: 4rem;
    height: 4rem;
    transform: translateY(-50%);
    border-radius: 50%;
    font-size: 0;
    outline: 0;
  }
  
  .slideshow::before,
  .containerback {
    left: -1rem;
  }
  
  .slideshow::after,
  .containernext {
    right: -1rem;
  }
  
  .slideshow::before,
  .slideshow::after {
    content: '';
    z-index: 1;
    background-color: none;
    background-size: 1.5rem 1.5rem;
    background-repeat: no-repeat;
    background-position: center center;
    color: #fff;
    font-size: 2.5rem;
    line-height: 4rem;
    text-align: center;
    pointer-events: none;
  }
  
  .slideshow::before {
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolygon points='0,50 80,100 80,0' fill='%23fff'/%3E%3C/svg%3E");
  }
  
  .slideshow::after {
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolygon points='100,50 20,100 20,0' fill='%23fff'/%3E%3C/svg%3E");
  }
  @media screen and (max-width: 600px){
    .slideshow{
      display: block;
    
    }
    .benz{
      width: 100%;
      height: 100%;
    }
  }